    angular
        .module('qsHub')
        .controller('TmMediaBrochuresSidebarController', [
            '$scope',
            '$resource',
            '$location',
            '$timeout',
            'constants',
            'NotifierFactory',
            'TmProfileFactory',
            'TmMediaBrochuresService',
            'TmMediaService',
            'WatchService',
            'UrlService',
            'ObjectService',
            App.controllers.TmMediaBrochuresSidebar
        ]);

